export * from './common';

/**
 *  THEME 
 * @param PRIMARY primary
 * @param WARNING warning
 * @param DANGER danger
 * @param SUCCESS success
 * @param INFO info
 * @param GREENSEA greensea
 * @param NEPHRITE nephrite
 * @param BELIZEHOLE belizehole
 * @param WISTERIA wisteria
 * @param ASPHALT asphalt
 * @param ORANGE orange
 * @param PUMPKIN pumpkin
 * @param POMEGRANATE pomegranate
 * @param CLOUDS clouds
 * @param ASBESTOS asbestos
 */
export enum THEME {
  PRIMARY = 'primary',
  WARNING = 'warning',
  DANGER = 'danger',
  SUCCESS = 'success',
  INFO = 'info',
  GREENSEA = 'greensea',
  NEPHRITE = 'nephrite',
  BELIZEHOLE = 'belizehole',
  WISTERIA = 'wisteria',
  ASPHALT = 'asphalt',
  ORANGE = 'orange',
  PUMPKIN = 'pumpkin',
  POMEGRANATE = 'pomegranate',
  CLOUDS = 'clouds',
  ASBESTOS = 'asbestos',
}

export const ThemeOption = [
  { label: 'PRIMARY', value: THEME.PRIMARY },
  { label: 'WARNING', value: THEME.WARNING },
  { label: 'DANGER', value: THEME.DANGER },
  { label: 'SUCCESS', value: THEME.SUCCESS },
  { label: 'INFO', value: THEME.INFO },
  { label: 'GREENSEA', value: THEME.GREENSEA },
  { label: 'NEPHRITE', value: THEME.NEPHRITE },
  { label: 'BELIZEHOLE', value: THEME.BELIZEHOLE },
  { label: 'WISTERIA', value: THEME.WISTERIA },
  { label: 'ASPHALT', value: THEME.ASPHALT },
  { label: 'ORANGE', value: THEME.ORANGE },
  { label: 'PUMPKIN', value: THEME.PUMPKIN },
  { label: 'POMEGRANATE', value: THEME.POMEGRANATE },
  { label: 'CLOUDS', value: THEME.CLOUDS },
  { label: 'ASBESTOS', value: THEME.ASBESTOS },
];

/**
 *  MODE 
 * @param FULL full  []
 * @param SIDE side 
 */
export enum MODE {
  FULL = 'full',
  SIDE = 'side',
}

/**
 *  LOGLEVEL 
 * @param DEBUG debug
 * @param INFO info
 * @param WARN warn
 * @param ERROR error
 * @param DISABLE disable
 */
export enum LOGLEVEL {
  DEBUG = 'debug',
  INFO = 'info',
  WARN = 'warn',
  ERROR = 'error',
  DISABLE = 'disable',
}

export const LogLevelOption = [
  { label: 'DEBUG', value: LOGLEVEL.DEBUG },
  { label: 'INFO', value: LOGLEVEL.INFO },
  { label: 'WARN', value: LOGLEVEL.WARN },
  { label: 'ERROR', value: LOGLEVEL.ERROR },
  { label: 'DISABLE', value: LOGLEVEL.DISABLE },
];
/**
 * 
 * @param name {string} 
 * @param author {string} 
 * @param version {string} 
 */
export interface Meta {
  name: string;
  author: string;
  version: string;
}

/**
 * @param container {string} ID
 * @param editable {boolean | false} 
 * @param theme {@link THEME} 
 * @param mode {@link MODE} 
 * @param support_html {boolean | true} HTML
 * @param log_level {@link LOGLEVEL} 
 */
export interface Options {
  container: string;
  editable: boolean | false;
  theme: THEME;
  mode: MODE;
  support_html: boolean | true;
  log_level: LOGLEVEL;
  view: View;
  layout: object;
  shortcut: object;
}

/**
 *  ENGINE 
 * @param CANVAS  canvas
 * @param SVG  svg
 */
export enum ENGINE {
  CANVAS = 'canvas',
  SVG = 'svg',
}

export const EngineOption = [
  { label: 'CANVAS', value: ENGINE.CANVAS },
  { label: 'SVG', value: ENGINE.SVG },
];

/**
 *  NODEOVERFLOW 
 * @param HIDDEN  hidden
 * @param WRAP  wrap
 */
export enum NODE_OVERFLOW {
  HIDDEN = 'hidden',
  WRAP = 'wrap',
}

export const NodeOverflowOption = [
  { label: 'HIDDEN', value: NODE_OVERFLOW.HIDDEN },
  { label: 'WRAP', value: NODE_OVERFLOW.WRAP },
];

/**
 * @param engine {@link ENGINE} 
 * @param hmargin {number} 
 * @param vmargin {number} 
 * @param line_width {number} 
 * @param line_color {string} html
 * @param draggable {boolean | true} 
 * @param hide_scrollbars_when_draggable {boolean | true}  draggable = true 
 * @param node_overflow {@link NODE_OVERFLOW} 
 */
export interface View {
  engine: ENGINE;
  hmargin: number;
  vmargin: number;
  line_width: number;
  line_color: string;
  draggable: boolean | true;
  hide_scrollbars_when_draggable: boolean | true;
  node_overflow: NODE_OVERFLOW;
}
